.page {
  box-sizing: border-box;
}

.page__body {
  margin: 0;
}

.page__main {
  margin: 0;
  padding: 0;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  margin-top: 5px;
}

.nav__list {
  gap: 20px;
  height: 60px;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  flex-direction: column;
  display: flex;
}

.nav__item:first-child:after {
  content: attr(div);
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
}

.nav__linkl {
  color: #060b35;
  text-transform: uppercase;
  white-space: nowrap;
  padding: 22.5px 0 19.5px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
}

.nav__linkl:hover, .nav .is-active {
  color: #00acdc;
  transition: color, all .3s;
}

.catalog {
  box-sizing: border-box;
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  transition: grid-template-columns .3s;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  transition: all .3s;
  display: flex;
}

.card__items {
  margin: 0 16px;
}

.card__items--photo {
  height: 134px;
  margin: 32px 19px 0;
}

.card__items--name {
  color: #060b35;
  margin-top: 40px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__items--code {
  color: #616070;
  margin-top: 4px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__items--grade {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-top: 16px;
  display: flex;
}

.card__items--grade .reviews {
  color: #060b35;
  text-align: right;
  justify-content: flex-end;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  line-height: 14px;
}

.card__items--price {
  flex-direction: row;
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.card__items--button {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  height: 40px;
  margin: 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: all .3s;
}

.card__items--button:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__items--name {
  color: #34568b;
}

.price {
  margin-top: 0;
  margin-bottom: 0;
  font-family: Roboto, sans-serif;
  line-height: 18px;
}

.price--text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
}

.price--number {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
}

.stars {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 96px;
  height: 16px;
  display: flex;
}

.stars :nth-child(n+1), .stars :nth-child(n+2), .stars :nth-child(n+3), .stars :nth-child(n+4), .stars :nth-child(n+5) {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--1 :nth-child(n+2), .stars--2 :nth-child(n+3), .stars--3 :nth-child(n+4), .stars--4 :nth-child(n+5), .stars--5 :nth-child(n+6) {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

