.card {
  display: flex;
  flex-direction: column;

  width: 200px;
  border-radius: 5px;
  border: 1px solid #F3F3F3;
  box-sizing: border-box;

  transition: .3s;

  &__items {
    margin: 0 16px;

    &--photo {
      height: 134px;
      margin: 32px 19px 0;
    }

    &--name {
      color: $main-accent;
      font-family: $font;
      font-size: 12px;
      font-weight: 500;
      line-height: 18px;

      margin-top: 40px;
    }

    &--code {
      color: $secondary;
      font-family: $font;
      font-size: 10px;
      line-height: 14px;
      font-weight: 400;

      margin-top: 4px;
    }

    &--grade {
      margin-top: 16px;
      height: 16px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;

        .reviews {
          color: $main-accent;
          text-align: right;
          font-family: $font;
          font-size: 10px;
          line-height: 14px;
          justify-content: flex-end;
        }

      }

    &--price {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin-top: 24px;
    }

    &--button {
        margin: 16px;
        height: 40px;
        border: 1px solid $high-light-color;
        border-radius: 5px;
        background: $high-light-color;

        color: $white;
        text-align: center;
        font-family: $font;
        font-size: 14px;
        font-weight: 700;
        line-height: 16px; /* 114.286% */
        text-transform: uppercase;
        transition: .3s;

        &:hover {
          background: $white;
          border-radius: 5px;
          border: 1px solid $high-light-color;
          color: $high-light-color;
        }
    }
  }

  &:hover {
    transform: scale(120%);
  }

  &:hover &__items--name {
    color: #34568b;

  }
}

.price {
  margin-top: 0;
  margin-bottom: 0;
  font-family: $font;
  line-height: 18px; /* 112.5% */

  &--text {
    color: $secondary;
    font-size: 12px;
    font-weight: 400;
  }

  &--number {
    color: $main-accent;
    text-align: right;
    font-size: 16px;
    font-weight: 700;
  }
}
